import React, { useEffect, useState } from 'react'

import Modal from '../components/Modal'

import '../assets/scss/Stake.scss'
import { Token_Data } from './Dashboard'

import ETH_WITH_CIRCLE from '../assets/imgs/Eth_with_circle.svg'
import Angle_down from '../assets/imgs/Angle-down.svg'
import Search_Icon from '../assets/imgs/Search.svg'
import BiDirection from '../assets/imgs/Bi-Direction.svg'
import FSD_Logo from '../assets/imgs/tokens/FSD.svg'
import ETH_Logo from '../assets/imgs/tokens/ETH.svg'
import Inch from '../assets/imgs/tokens/1inch.svg'
import Polkadot from '../assets/imgs/tokens/Polkadot.svg'


const Stake = () => {
  const [isStake, setIsStake] = useState(true)
  const [value, setValue] = useState(0)
  const [searchKey, setSearchKey] = useState('')
  const [isOpen, setIsOpen] = useState(false)
  const [selectedToken, setSelectedToken] = useState(Token_Data[0])

  const selectToken = (token) => {
    setSelectedToken(token)
    setIsOpen(false)
  }

  useEffect(() => {
    if (!isStake) {
      setSelectedToken(Token_Data[1])
    } else {
      setSelectedToken(Token_Data[0])
    }

  }, [isStake])

  return (
    <div className='Stake'>
      <div className='toggle-button'>
        <div className={isStake ? "active" : ''} onClick={() => setIsStake(true)}>Stake</div>
        <div className={isStake ? "" : 'active'} onClick={() => setIsStake(false)}>Unstake</div>
      </div>
      <div className='body'>
        <div>
          <div className='row'>
            <div className='col-lg-4 body-card'>
              <img src={ETH_WITH_CIRCLE} alt='' />
              <h4 style={{ 'color': '#5A6169', 'marginBottom': '20px' }}>ETH Staking</h4>
              <div className='li-wrapper'>
                <li><span>Stake any amount of ETH to earn staking rewards</span></li>
                <li><span>Convert your ERC 20 tokens with the 1inch router to stake your tokens of choice</span></li>
                <li><span>Your selected ERC-20 tokens will be routed through 1inch and za for staking on Fairside. Best price routes costs ~$15.61 for fees. This route optimizes your input considering splits routes, hops and  gas cost of each step.</span></li>
                <li><span>Increase your conviction score by staking more than 1 ETH.</span></li>
              </div>
              <h4 className='pri-color mt-30'>Rewords: 5.99%</h4>
            </div>
            <div className='col-lg mt-md-3 mt-lg-0'>
              {/* <div className='container'>
                <div className='row'>
                  <div className='col-md stake-card' style={{ 'marginRight': '20px' }}>
                    <p>Total Claimable</p>
                    <p className='fs-31'>515.15 FSD</p>
                  </div>
                  <div className='col-md stake-card'>
                    <p>Total Deposits</p>
                    <p className='fs-31'>515.15 FSD</p>
                  </div>
                </div>
              </div> */}
              <div className='inch-card'>
                <div className='mt-lg-3 mt-md-0'>
                  <p className='sec-dark-color fs-21'>
                    {
                      isStake ? 'Stake ETH or ERC-20 Token:' : 'Unstake to withdraw your ETH:'
                    }
                  </p>
                  {
                    isStake ? <div className='inch-select' onClick={() => setIsOpen(true)}>
                      <img src={selectedToken.logo} alt='' />
                      {
                        selectedToken.name
                      }
                      <img src={Angle_down} alt='angle-down-icon' />
                    </div> : <div className='d-flex flex-row'>
                      <div className='inch-select'>
                        <img src={FSD_Logo} alt='' style={{ 'marginLeft': 0 }} />
                        FSD
                      </div>
                      <img src={BiDirection} alt='' style={{ 'margin': '0 20px' }} />
                      <div className='inch-select'>
                        <img src={ETH_Logo} alt='' style={{ 'marginLeft': 0 }} />
                        ETH
                      </div>
                    </div>
                  }
                  {
                    !isStake && <>
                      <div className='unstake-info'>
                        <div className='d-flex justify-content-between'>
                          <p>ETH Received:</p>
                          <p className='pri-color'>3.3 ETH</p>
                        </div>
                        <div className='d-flex justify-content-between'>
                          <p>FSD Burned:</p>
                          <p className='pri-color'>303,431</p>
                        </div>
                      </div>
                    </>
                  }

                </div>
                <div>
                  <div className='d-flex flex-row justify-content-between inch-input-description'>
                    <p>
                      {
                        isStake ? `Amount of ${selectedToken.name} to stake` : 'Amount of FSD to unstake'
                      }
                    </p>
                    <p onClick={() => setValue(3400)}>Available: 3400.00</p>
                  </div>
                  <input type='number' value={value} onChange={(e) => setValue(e.target.value)} min={0} className='input-box' />
                </div>
                {
                  isStake ? <div className='border-05 radius-10 p-10 d-flex flex-column mt-10'>
                    <div className='d-flex justify-content-between'>
                      <p className='mb-5'>Slippage Fee</p>
                      <p className='mb-5 pri-color'>0.0034 ETH</p>
                    </div>
                    <div className='d-flex justify-content-between'>
                      <p className='mb-5'>Amount of FSD received</p>
                      <p className='mb-5 pri-color'>145,314.00 FSD</p>
                    </div>
                    <div className='d-flex justify-content-between'>
                      <p className='mb-5'>Conviction Score Gained</p>
                      <p className='mb-5 pri-color'>0.145</p>
                    </div>
                    {
                      selectedToken.name !== 'ETH' && <>
                        <div>
                          <p className='mb-10'>Router ERC-20:</p>
                          <div className='d-flex flex-row justify-content-between router'>
                            <button><img src={selectedToken.logo} alt='' />100%</button>
                            <div className='d-flex'></div>
                            <button><img src={Inch} alt='' height='20px' />0.05%</button>
                            <div className='d-flex'></div>
                            <button><img src={ETH_Logo} alt='' />100%</button>
                          </div>
                        </div>
                      </>
                    }

                  </div> : <div className='border-05 radius-10 p-10 d-flex flex-column mt-10'>
                    <div className='d-flex justify-content-between'>
                      <p className='mb-5'>Tribute Fee</p>
                      <p className='mb-5 pri-color'>4.5%</p>
                    </div>
                    <div className='d-flex justify-content-between'>
                      <p className='mb-5'>Conviction Score Loss</p>
                      <p className='mb-5 pri-color'>-141</p>
                    </div>
                    <div className='d-flex justify-content-between'>
                      <p className='mb-5'>ETH Received</p>
                      <p className='mb-5 pri-color'>3.3 ETH</p>
                    </div>
                    <div className='d-flex justify-content-between'>
                      <p className='mb-5'>FSD Burned</p>
                      <p className='mb-5 pri-color'>303,431</p>
                    </div>
                  </div>
                }
                <p className='inch-description mt-10'>
                  {

                    isStake ? 'Your selected ERC-20 tokens will be routed through 1inch and zapped for staking on Fairside. Best price routes costs ~$15.61 for fees. This route optimizes your input considering splits routes, hops and  gas cost of each step.'
                      : 'Unstake/redeem your ERC-20 tokens and receive the equivalent amount of ETH after a few days. Withdraw fee will be included with the total.'
                  }
                </p>
                <div className='inch-buttons'>
                  {
                    isStake ? <><button className='approved button-hover-shadow'>Approve</button>
                      <button className='disabled' disabled>Stake</button></>
                      : <><button className='approved button-hover-shadow'>Unstake</button>
                        <button className='disabled' disabled>Stake</button></>
                  }

                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Modal isOpen={isOpen} setIsOpen={setIsOpen}>
        <div className='search-modal'>
          <p className='title'>Select a Token</p>
          <div className="search-input">
            <img src={Search_Icon} alt='search-icon' />
            <input type="text" placeholder='Search by name or paste address' value={searchKey} onChange={(e) => setSearchKey(e.target.value)} />
          </div>
          <div className='token-list-wrapper'>
            {
              Token_Data.map((token, index) => {
                if (token.name === 'FSD') return ''
                if (token.name.toLowerCase().indexOf(searchKey.toLowerCase()) < 0) return ''
                return (
                  <div key={index} className='token-list' onClick={() => selectToken(token)}>
                    <div className='token-name-wrapper'>
                      <img src={token.logo} alt='' height='40px' />
                      <div className='token-name'>
                        <p>{token.name}</p>
                        <p>{token.name}</p>
                      </div>
                    </div>
                    <div className='token-value'>
                      {token.value}
                    </div>
                  </div>
                )
              })
            }
          </div>
        </div>
      </Modal>
    </div>
  )
}

export default Stake